// reducer
let reducerList = (state={listData:[]},action) => {
    switch (action.type){
        case "GET_LOAD_LIST_DATA":
            console.log("...");
            return state;
            break;
        case "GET_LOAD_LIST_DATA_SUCCESS":
            console.log("...");
            // 
            return {listData:action.data};
            break;
        case "GET_LOAD_LIST_DATA_ERROR":
            console.log("");
            return state;
            break;
        default:
            return state;
    }
};

export {reducerList};


