// 
let reducerMine = (state={pageTitle:""},action) => {
    switch (action.type){
        case "SHOW_DETAIL":
            return "";
            break;
        case "SHOW_TITLE":
            return {pageTitle:state.pageTitle};
            break;
        default:
            return state;
    }
};
export {reducerMine};


