// reeducer
let reducerHome = (state={homeData:[]},action) => {
    // action
    switch (action.type){
        case "GET_HOME_DATA_START":
            console.log("");
            return {homeData:[]};
            break;
        case "GET_HOME_DATA_SUCCESS":
            console.log("");
            console.log(action.data);
            //homeData = action.data;
            return{homeData:action.data};
            break;
        case "GET_HOME_DATA_ERROR":
            console.log("");
            return state;
            break;
        default:
            return state;
            break;
    }
};

export {reducerHome};


